import { HttpClient } from "@angular/common/http";
import { Component } from "@angular/core";
import { Storage } from "@ionic/storage";
import { NavController, App, ActionSheetController, LoadingController } from "ionic-angular";
// import { CallNumber } from "@ionic-native/call-number";
import { EmailComposer } from "@ionic-native/email-composer";
import { Toast } from "@ionic-native/toast";
import { ImagePicker } from "@ionic-native/image-picker";


@Component({
  selector: "page-profile",
  templateUrl: "profile.html"
})
export class ProfilePage {
  user: any;
  load: any;

  constructor(
    public navCtrl: NavController,
    public storage: Storage,
    public app: App,
    public actionSheetCtrl: ActionSheetController,
    private emailComposer: EmailComposer,
    public http: HttpClient,
    public loadingCtrl: LoadingController,
    public toast: Toast,
    // private callNumber: CallNumber,
    private imagePicker: ImagePicker
  ) {
    this.storage.get("user").then(user => {
      if (user !== null) {
        this.user = user;
      }
    });
  }

  changePhoto() {
    this.imagePicker
      .getPictures({
        maximumImagesCount: 1,
        quality: 95,
        outputType: 1,
        width: 500,
        height: 500
      })
      .then(
        image => {
          if (image.length > 0) {
            this.user.photo = "data:image/jpeg;base64," + image[0];
            this.storage.set("user", this.user).then(() => {
              this.http
                .patch("https://realm-glory.herokuapp.com/api/users", {
                  user_id: this.user._id,
                  photo: "data:image/jpeg;base64," + image[0]
                })
                .subscribe(status => {
                  console.log("uploaded", status);
                });
            });
          }
        },
        err => {}
      );
  }

  contact() {
    const actionSheet = this.actionSheetCtrl.create({
      title: "Talk to us",
      buttons: [
        {
          text: "Call us",
          icon: "ios-call-outline",
          handler: () => {
            this.callUs();
          }
        },
        {
          text: "Email us",
          icon: "ios-mail-outline",
          handler: () => {
            this.emailUs();
          }
        }
      ]
    });
    actionSheet.present();
  }

  callUs() {
    // this.callNumber
    //   .callNumber("+2348094987030", true)
    //   .then(res => console.log("Launched dialer!", res))
    //   .catch(err => console.log("Error launching dialer", err));
  }

  emailUs() {
    let email = {
      to: "pastor@realmofglory.org",
      bcc: [
        "olamide.steven@realmofglory.org",
        "leslie.bapetel@realmofglory.org"
      ]
    };
    this.emailComposer.open(email);
  }

  cloudSync() {
    this.load = this.loadingCtrl.create({
      content: "Syncing...",
      spinner: "dots"
    });
    this.load.present();

    // Sync Notes
    this.storage.get("notes").then(notes => {
      if (notes) {
        this.http
          .get(`https://realm-glory.herokuapp.com/api/notes/${this.user._id}`)
          .subscribe(
            data => {
              if (notes.length > data["data"].length) {
                let a = data["data"];
                let b = notes;
                const diffBy = pred => (a, b) =>
                  a.filter(x => !b.some(y => pred(x, y)));
                const makeSymmDiffFunc = pred => (a, b) =>
                  diffBy(pred)(a, b).concat(diffBy(pred)(b, a));
                const myDiff = makeSymmDiffFunc(
                  (x, y) => x.value === y.value && x.display === y.display
                );
                const result = myDiff(a, b);
                result.forEach(elem => {
                  this.uploadNotes(elem);
                });
                this.syncSermon();
                this.toast.showLongBottom("Sync completed").subscribe(_ => {});
              } else if (notes.length < data["data"]) {
                this.syncSermon();
                this.storage.set("notes", data["data"]);
                this.toast.showLongBottom("Sync completed").subscribe(_ => {});
                this.syncSermon();
              } else {
                this.toast.showLongBottom("Sync completed").subscribe(_ => {});
              }
            },
            err => {
              this.load.dismiss();
              console.log("err", err);
            }
          );
      } else {
        this.http
          .get(`https://realm-glory.herokuapp.com/api/notes/${this.user._id}`)
          .subscribe(data => {
            this.storage.set("notes", data["data"]);
            this.syncSermon();
          });
      }
    });
  }

  // Sync Sermons
  syncSermon() {
    this.http
      .get("https://realm-glory.herokuapp.com/api/sermons")
      .subscribe(sermons => {
        this.storage.set("sermons", sermons["data"]);
        this.load.dismiss();
      });
  }

  uploadNotes(note) {
    this.http
      .post("https://realm-glory.herokuapp.com/api/notes", {
        title: note.title,
        content: note.content,
        user: this.user._id
      })
      .subscribe(status => {
        this.storage.set("notes", status["data"]);
      });
  }

  locations() {
    this.navCtrl.push("LocationsPage");
  }

  allPush() {
    this.navCtrl.push("NotificationsPage");
  }

  quotes() {
    this.navCtrl.push("QuotesPage");
  }

  support() {
    this.navCtrl.push("SupportPage");
  }

  audioPlayer() {
    this.navCtrl.push("PlaylistPage");
  }

  legal() {
    this.navCtrl.push("PrivacyPage");
  }

  login() {
    this.app.getRootNavs()[0].setRoot("LandingPage");
  }

  prayers() {
    this.navCtrl.push("PrayersPage");
  }

  logOut() {
    this.storage.clear().then(() => {
      this.app.getRootNavs()[0].setRoot("LandingPage");
    });
  }
}



